file, and also import other style files */\n@import '~@angular/material/prebuilt-themes/indigo-pink.css';\n@import '~bootstrap-4-grid/scss/grid';\n\n@import '~@angular/material/theming';\n\n@include mat-core();\n\n$primary: mat-palette($mat-indigo, 700);\n$accent: mat-palette($mat-deep-orange, 500, A100, A400);\n$warn: mat-palette($mat-red, 600);\n\n$theme: mat-light-theme($primary, $accent, $warn);\n\n@include angular-material-theme($theme);\n\nbody,\nhtml {\n  margin: 0;\n  padding: 0;\n  font-family: Roboto, sans-serif;\n  height: 100%;\n  background-color: $dark;\n  position: fixed;\n  left: 0;\n  right: 0;\n  top: 0;\n  bottom: 0;\n  max-width: 100%;\n  overflow-x: hidden;\n  overflow-y: hidden;\n}\n\n.h100 {\n  height: 100%;\n}\n\n.list-item-active {\n  font-weight: bold;\n  color: mat-color($primary, darker) !important;\n  background: rgba(0, 0, 0, 0.04);\n}\n\n.border-left {\n  border-left: 4px solid mat-color($primary, darker) !important;\n}\n\n.centered {\n  position: fixed;\n  top: 50%;\n  left: 50%;\n  margin-top: -50px; //Half of the height of the loading graphic\n  margin-left: -50px; //Half of the width of the loading graphic\n  &:before {\n    content: '';\n    display: block;\n    position: fixed;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    background-color: rgba(0, 0, 0, 0.3);\n  }\n}\n\ntable {\n  width: 100%;\n}\n\n.mat-spinner {\n  z-index: 1000;\n}\n\n/***  Fonts ***/\n.f {\n  &-50 {\n    font-size: 50px;\n  }\n  &-small {\n    font-size: 1rem;\n  }\n  &-medium {\n    font-size: 1.5rem;\n  }\n  &-large {\n    font-size: 2rem;\n  }\n}\n\n.float {\n  &-right {\n    float: right;\n  }\n  &-left {\n    float: left;\n  }\n}\n\n.notify-success {\n  color: white !important; // green\n  background-color: green !important;\n  border-color: green !important;\n  font-size: 20px;\n  .mat-simple-snackbar-action {\n    color: white !important;\n  }\n}\n.mat-drawer-content {\n  overflow: hidden !important;\n}\n\n/**** Drag ****/\n/* i